import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'principal',
    component:()=>import('@/views/principal')
  },
  {
    path: '/contacto',
    name: 'contacto',
    component:()=>import('@/views/contacto')
  
  },
  {
    path:'/cotizacion',
    name:'cotizacion',
    component:()=>import('@/views/cotizacion')
  },
]

const router = new VueRouter({
  routes
})

export default router
